@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500;700&display=swap);
.movie {
  display: flex;
  align-items: flex-start;
  position: relative;
  width: 420px;
  margin: 25px 0;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
  position: relative;
  top: -40px;
  width: 120px;
  margin-right: 20px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie__year {
  margin: 10px 0;
  opacity: 0.3;
}

.genres {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 15px;
}

.genres__genre {
  margin-right: 7px;
  opacity: 0.6;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

li {
  list-style: none;
}

body {
  overflow-x: hidden;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  min-height: 100vh;
  background-color: #fdfce5;
  font-family: "Quicksand", sans-serif;
}

.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  color: #333;
  font-size: 30px;
}

.movies {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 900px;
  margin: 0 auto;
  padding: 88px 10px 20px;
}

.movies a {
  display: contents;
  text-decoration: none;
}

.about__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 450px;
  height: 180px;
  margin: 118px auto 50px;
  padding: 30px;
  font-size: 18px;
  background-color: #fff;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.about__container span {
  width: 100%;
  text-align: right;
}

.detail__container {
  display: flex;
  align-items: flex-start;
  width: 90%;
  margin: 128px auto 30px;
  padding: 30px;
  background-color: #fff;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.detail__container img {
  position: relative;
  top: -60px;
  width: 30%;
  margin-right: 5%;
  border-radius: 10px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.detail__container .detail__info {
  width: 65%;
}

.detail__container .detail__title {
  margin: 10px 0;
  font-size: 30px;
}

.detail__container .detail__genres {
  display: flex;
  margin-bottom: 10px;
}

.detail__container .detail__genres li {
  margin: 0 5px;
  opacity: 0.6;
}

.detail__container .detail__summary {
  font-size: 18px;
  line-height: 1.5;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  padding: 15px 0;
  background-color: #d7e9f7;
}

.nav a {
  margin: 0 10px;
  padding: 8px 20px;
  color: #333;
  font-size: 14px;
  font-weight: 700;
  border: 2px solid #f4d19b;
  border-radius: 30px;
  background-color: #f4d19b;
  transition: all 0.2s ease;
}

.nav a:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

